<template>
  <div class="content-div-Page home-div" :class="home_status" id="content-div-Page">
    <div class="header">
      <div class="pageBack-btn" @click="pageBack()">
        <img src="@/assets/images/icon-arrow.svg" height="25" alt="">
      </div>
      <span>{{homeId}}</span>
      
    </div>
    <div class="home-div-cover">
      <div v-for="i in 1000" :key="i">{{user_id}}</div>
    </div>
    <div class="page-div" id="page-div">

      <div class="container-fluid">
        <div class="row pb-3">
          <!-- <div class="col-12 mt-3">
            <h2>Customer Profile</h2>
          </div> -->
          
          <div class="col-12 col-sm-12 col-md-12 col-lg-6 col-xl-4">
            <div class="box">
              <div class="content">
                <canvas id="chartCustomerProfile"></canvas>
              </div>
              <div class="mt-0" style="font-size: 10px;color: rgba(255,255,255,0.5)">點擊圖表顯示該區資訊</div>
            </div>
          </div>
          <div class="col-12 col-sm-12 col-md-12 col-lg-6 col-xl-8">
            <div class="box">
              <div class="title">
                {{dataName}}
              </div>
              <div class="content">
                <table class="customerTable">
                  <thead>
                    <tr>
                      <th style="text-align: center">處級名稱</th>
                      <th style="text-align: center">全球實銷佔比</th>
                      <th style="text-align: center">年度達標率</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(key,index) in tableData" :key="index" @click="openCustomerProfileSubPage(index)">
                      <td align="center">{{key.Name}}</td>
                      <td align="center">{{key.Sales}}</td>
                      <td align="center">{{key.Targets}}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <div class="mt-0" style="font-size: 10px;color: rgba(255,255,255,0.5)">點擊表格顯示詳細資訊</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
import Chart from 'chart.js'

var _chart01;

export default {
  props: {
  },
  mounted:function(){
    this.initHeight();
    window.onresize = () => {
      this.initHeight();
    } 
    this.init() 
  },
  watch: {
    customerProfileData(newCount, oldCount) {
      if(newCount != oldCount){
        this.setChart();
        this.tableData = this.customerProfileData[0].Children
      }
    }
        
  },
  computed: {
    home_status() {
      return this.$store.state.home_status;
    },
    homeId(){
      return this.$store.state.homeId;
    },
    customerProfileData() {
      return this.$store.state.customerProfileData;
    },
    mainId(){
      return this.$store.state.mainId
    },
    user_id(){
      return this.$store.state.GBLoginInfo.user_id;
    }
  },
  data(){
    return {
       tableData: null,
       chartData: null,
       dataName: null
    }
  },
  methods:{
    initHeight(){
      var h = window.innerHeight;
      var hh = window.innerHeight - 50;
      document.getElementById('content-div-Page').setAttribute("style","height:"+h+"px");
      document.getElementById('page-div').setAttribute("style","height:"+hh+"px");
    },
    init(){
      this.setChart();
      this.tableData = this.customerProfileData[0].Children;
      this.dataName =  this.customerProfileData[0].Name;
    },
    setChart(){
      if(_chart01){
        _chart01.destroy();
      }
      this.chartData = [[],[]];
      let chartColor = this.$store.state.chartColor;

      for(var key in this.customerProfileData){
        this.chartData[0].push(this.customerProfileData[key].Name)
        this.chartData[1].push(this.customerProfileData[key].Sales)
      }

      console.log(this.chartData);
      

      var ctx01 = document.getElementById('chartCustomerProfile');
      _chart01 = new Chart(ctx01, {
          type: 'doughnut',
          data: {
              labels: this.chartData[0],
              datasets: [{
                  label: this.chartData[0],
                  data: this.chartData[1],
                  backgroundColor: chartColor,
                  borderColor: chartColor,
                  borderWidth: 0,
              }],
          },
          options: {
            aspectRatio: 2,
            // cutoutPercentage: 50
            legend: {
              labels: {
                  width: 50,
                  fontColor: '#fff',
                  fontFamily: 'Microsoft JhengHei'
              },
              position : "left"
            },
            // title: {
            //     display: false,
            //     text: 'Custom Chart Title'
            // },
            // layout: {
            //     padding: {
            //         left: 0,
            //         right: 0,
            //         top: 0,
            //         bottom: 0
            //     }
            // }
          }
      });
      let _this = this;
      ctx01.onclick = function(e) {
        var slice = _chart01.getElementAtEvent(e);
        if (!slice.length) return; // return if not clicked on slice
        if(_this.customerProfileData[slice[0]._index].Children){
          _this.tableData = _this.customerProfileData[slice[0]._index].Children;
          _this.$store.state.mainId = slice[0]._index;
        }else{
          _this.tableData = null;
        }

        _this.dataName =  _this.customerProfileData[slice[0]._index].Name;
        
      }
    },
    openCustomerProfileSubPage(pageId){
      
      if(this.$store.state.customerProfileData[this.$store.state.mainId].Children[pageId].Children){
        this.$store.state.pageId = pageId;
        this.$store.commit('openPage');
      }
    },
    
    pageBack(){
     this.$store.state.home_status = '';
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped src="@/assets/css/page.css"></style>
<style scoped>
  .content-div-Page{
  position: fixed;
  z-index: 10;
  top: 0;
  left: 100%;
  width: 100%;
  height: 100vh;
  overflow-y: hidden;
  overflow-x: hidden;
  /* background: #212335; */
    background: #4e505f;
    transition: 0.3s all;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
    background: linear-gradient(135deg,#4e505f,#212335);
  /* background: linear-gradient(180deg,#4e505f,#212335,#4e505f); */
  /* background: linear-gradient(180deg, #43648B 0%, #6FA1D1 48.44%, #2F76B8 100%); */
  /* background: linear-gradient(90deg, #43648B 0%, #6FA1D1 48.44%, #2F76B8 100%); */
}

.content-div-Page.active{
  left: 0;
}

.page-div{
  position: absolute;
  z-index: 1;
  top: 50px;
  width: 100%;
  left: 0;
  overflow-y: auto;
  overflow-x: hidden;
  /* background: #f00; */
}

.home-div-cover{
    position: fixed;
    z-index: 0;
    width: 130%;
    height: 100%;
    left: -15%;
    top: 0px;
    overflow: hidden;
    text-align: center;
}

.home-div-cover div{
    position: relative;
    display: inline-block;
    width: 125px;
    text-align: center;
    line-height: 36px;
    font-size: 30px;
    color: rgba(255,255,255,0.05);
    transform: rotate(30deg);
}

.window-box-cover{
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0px;
  width: 100%;
  height: calc(100% - 0px);
  background: rgba(0,0,0,0.7);
}
.window-box{
  position: relative;
  margin: 0 auto;
  margin-top: calc(50vh - 25vh);
  /* display: inline-block; */
  width: calc(100% - 30px);
  max-width: 500px;
  background: rgba(62, 65, 79,1);
  border-radius: 5px;
  padding: 15px;
  min-height: 50vh;
}

.header{
    position: absolute;
    width: 100%;
    height: 50px;
    top: 0;
    left: 0;
    z-index: 1;
    /* background: #212335; */
    background: #181a2b;
    padding: 0;
    line-height: 50px;
    color: #fff;
    text-align: center;
  }

  .pageBack-btn{
    position: absolute;
    left: 0;
    top: 0;
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    cursor: pointer;
    transition: 0.2s all;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
  }

  .pageBack-btn:hover{
    background: #4e505f;
  }

  .pageBack-btn img{
    transform: rotate(-90deg);
  }

</style>
