<template>
  <div class="content-div-subpage home-div" :class="subpage_status" id="content-div-subpage">
    <div class="header">
      <div class="pageBack-btn" @click="pageBack()">
        <img src="@/assets/images/icon-arrow.svg" height="25" alt="">
      </div>
      <span>{{subPageData.Name}}</span>
      <span class="userLeveldiv px-3">
        <img src="@/assets/images/icon-good.svg" v-if="subPageData.Level==2 || subPageData.Level==1 " height="20" alt="">
        <img src="@/assets/images/icon-good.svg" v-if="subPageData.Level==2 || subPageData.Level==1 " height="20" alt="">
        <img src="@/assets/images/icon-good.svg" v-if="subPageData.Level==2" height="20" alt="">
        <img src="@/assets/images/icon-bad.svg" v-if="subPageData.Level==0" height="20" alt="">
      </span>
    </div>
    <div class="home-div-cover">
      <div v-for="i in 1000" :key="i">{{user_id}}</div>
    </div>
    <div class="subPage-div" id="subPage-div">
      <div class="container-fluid">
        <div class="row">
          <div class="col-12 col-sm-12 col-md-12 col-lg-5 col-xl-4">
            <div class="box p-0">
              <div class="title">
              </div>
              <div class="content">
                <carousel class="my-custom-class" :per-page="1" :mouse-drag="true" :perPageCustom="[[480, 2], [768, 2]]" :paginationActiveColor="'rgba(255,255,255,1)'" :paginationColor="'rgba(255,255,255,0.3)'">
                  <slide>
                    <canvas id="subPageChart01" width="100%"></canvas>
                  </slide>
                  <slide>
                    <canvas id="subPageChart02" width="100%"></canvas>
                  </slide>
                </carousel>
              </div>
            </div>
          </div>
          
          <div class="col-12 col-sm-12 col-md-12 col-lg-7 col-xl-8">
            <div class="box">
              <div class="slideBox">
                <div class="slideTitle" @click="openSlide(1)">Base Info 
                  <img src="@/assets/images/icon-plus.svg" v-if="slideStatus_1 == ''" height="20" alt="">
                  <img src="@/assets/images/icon-less.svg" v-if="slideStatus_1 != ''" height="20" alt="">
                </div>
                
                  <div class="slideContent" v-if="slideStatus_1=='active'">
                    <table class="table_type_1">
                        <tr>
                          <th><span>客戶代號</span></th>
                          <td>{{customerData.BaseInfo.CustomerID}}</td>
                        </tr>
                        <tr>
                          <th><span>信用評等</span></th>
                          <td>{{customerData.BaseInfo.CustomerLevel}}</td>
                        </tr>
                        <tr>
                          <th><span>國別</span></th>
                          <td>{{customerData.BaseInfo.Country}}</td>
                        </tr>
                        <tr>
                          <th><span>子公司否</span></th>
                          <td>{{customerData.BaseInfo.Subsidiary}}</td>
                        </tr>
                        <tr>
                          <th><span>業務窗口</span></th>
                          <td>{{customerData.BaseInfo.CustomerSales}}</td>
                        </tr>
                        <tr>
                          <th><span>客戶聯絡人</span></th>
                          <td>{{customerData.BaseInfo.ContactInfo}}</td>
                        </tr>
                    </table>
                  </div>
                
              </div>
              <div class="slideBox">
                <div class="slideTitle" @click="openSlide(2)">Order
                  <img src="@/assets/images/icon-plus.svg" v-if="slideStatus_2 == ''" height="20" alt="">
                  <img src="@/assets/images/icon-less.svg" v-if="slideStatus_2 != ''" height="20" alt="">
                </div>
                
                <div class="slideContent" v-if="slideStatus_2=='active'">
                  <table class="table_type_2">
                    <thead>
                      <tr>
                        <th v-for="(key,index) in customerData.Order.title" :key="index"><span>{{key}}</span></th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(key,index) in customerData.Order.row" :key="index">
                        <td>{{key[0]}}</td>
                        <td>{{key[1]}}</td>
                        <td>{{key[2]}}</td>
                        <td>{{key[3]}}</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                
              </div>
              <div class="slideBox">
                <div class="slideTitle" @click="openSlide(3)">AR 
                  <img src="@/assets/images/icon-plus.svg" v-if="slideStatus_3 == ''" height="20" alt="">
                  <img src="@/assets/images/icon-less.svg" v-if="slideStatus_3 != ''" height="20" alt="">
                </div>
                
                <div class="slideContent" v-if="slideStatus_3=='active'">
                  <table class="table_type_2">
                    <thead>
                      <tr>
                        <th v-for="(key,index) in customerData.AR.title" :key="index"><span>{{key}}</span></th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(key,index) in customerData.AR.row" :key="index">
                        <td>{{key[0]}}</td>
                        <td>{{key[1]}}</td>
                        <td>{{key[2]}}</td>
                        <td>{{key[3]}}</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                
              </div>
              <div class="slideBox">
                <div class="slideTitle" @click="openSlide(4)">Rebate
                  <img src="@/assets/images/icon-plus.svg" v-if="slideStatus_4 == ''" height="20" alt="">
                  <img src="@/assets/images/icon-less.svg" v-if="slideStatus_4 != ''" height="20" alt="">
                </div>
                
                <div class="slideContent" v-if="slideStatus_4=='active'">
                  <table class="table_type_2">
                    <thead>
                      <tr>
                        <th v-for="(key,index) in customerData.Rebate.title" :key="index"><span>{{key}}</span></th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr v-for="(key,index) in customerData.Rebate.row" :key="index">
                        <td>{{key[0]}}</td>
                        <td>{{key[1]}}</td>
                        <td>{{key[2]}}</td>
                        <td>{{key[3]}}</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                
              </div>
              <div class="slideBox">
                <div class="slideTitle" @click="openSlide(5)">Sell Out
                  <img src="@/assets/images/icon-plus.svg" v-if="slideStatus_5 == ''" height="20" alt="">
                  <img src="@/assets/images/icon-less.svg" v-if="slideStatus_5 != ''" height="20" alt="">
                </div>
                
                <div class="slideContent" v-if="slideStatus_5=='active'">
                  <table class="table_type_2">
                    <thead>
                      <tr>
                        <th><span></span></th>
                        <th v-for="(key,index) in customerData.SellOut.title" :key="index"><span>{{key}}</span></th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td><span>Sell out</span></td>
                        <td v-for="(key,index) in customerData.SellOut.sellOut" :key="index">{{key}}</td>
                      </tr>
                      <tr>
                        <td><span>Inv.</span></td>
                        <td v-for="(key,index) in customerData.SellOut.inv" :key="index">{{key}}</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                
              </div>
              <div class="slideBox">
                <div class="slideTitle" @click="openSlide(6)">近四週庫存週數
                  <img src="@/assets/images/icon-plus.svg" v-if="slideStatus_6 == ''" height="20" alt="">
                  <img src="@/assets/images/icon-less.svg" v-if="slideStatus_6 != ''" height="20" alt="">
                </div>
                
                <div class="slideContent" v-if="slideStatus_6=='active'">
                  <table class="table_type_2">
                    <thead>
                      <tr>
                        <th v-for="(key,index) in customerData.LastStock.title" :key="index"><span>{{key}}</span></th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td v-for="(key,index) in customerData.LastStock.row1" :key="index">{{key}}</td>
                      </tr>
                      <tr>
                        <td v-for="(key,index) in customerData.LastStock.row2" :key="index">{{key}}</td>
                      </tr>
                    </tbody>
                  </table>
                </div>
                
              </div>
             
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Carousel, Slide } from 'vue-carousel';
import Chart from 'chart.js'
var _chart01;
var _chart02;

export default {
  components: {
    Carousel,
    Slide
  },
  props: {

  },
  watch: {
        
  },
  computed: {
    subpage_status() {
      return this.$store.state.subpage_status;
    },
    subPageId(){
      return this.$store.state.subPageId
    },
    subPageDataId(){
      return this.$store.state.subPageDataId
    },
    subPageData(){
      if(this.$store.state.customerProfileData[this.$store.state.mainId].Children[this.$store.state.pageId].Children[this.$store.state.pageId].Children[this.$store.state.subPageDataId] != null){
        return this.$store.state.customerProfileData[this.$store.state.mainId].Children[this.$store.state.pageId].Children[this.$store.state.pageId].Children[this.$store.state.subPageDataId]
      }else{
        return null
      }
    },
    user_id(){
      return this.$store.state.GBLoginInfo.user_id;
    }
  },
  mounted:function(){
    this.initHeight();
    window.onresize = () => {
      this.initHeight();
    }
    this.setChart();
  },
  data(){
    return{
      //狀態
      slideStatus_1: 'active',
      slideStatus_2: '',
      slideStatus_3: '',
      slideStatus_4: '',
      slideStatus_5: '',
      slideStatus_6: '',

      //data 1
      baseInfoData:{
        id: '',
        Level: '',
        country: '',
        company: '',
        business:'',
        customerContact:''
      },
      orderData:[],

      customerData : {
        "chart": {
          "chart1":{
            "name":"曝顯比例",
            "title":["2019W37","2019W38","2019W39"],
            "row":['70','15','15']
          },
          "chart2":{
            "name":"今年全球實銷",
            "title":["2019W37","2019W38","2019W39"],
            "row":['700000','450000','670000'],
          }
        },
        "BaseInfo": {
          "CustomerID":"08211",
          "CustomerLevel":"B1",
          "Country":"South Korea",
          "Subsidiary":"N",
          "CustomerSales":"TA150460 李美君",
          "ContactInfo":"Mr.Inwoo"
        },
        "Order":{
          "title":["日期","訂單單號","總數量","總金額"],
          "row":{
            "0":["19/08/21","UO1-12345","78000","NTD 19500"],
            "1":["19/08/21","UO1-12345","78000","NTD 19500"],
            "2":["19/08/21","UO1-12345","78000","NTD 19500"],
          }
        },
        "AR":{
          "title":["AR單號","帳款日期","應收金額","已沖金額"],
          "row":{
            "0":["aaa1","bbb1","ccc1","ddd1"],
            "1":["aaa2","bbb2","ccc2","ddd2"],
            "2":["aaa3","bbb3","ccc3","ddd3"]
          }
        },
        "Rebate":{
          "title":["銷退單號","銷退日期","出貨單號","銷退金額"],
          "row":{
            "0":["aaa1","bbb1","ccc1","ddd1"],
            "1":["aaa2","bbb2","ccc2","ddd2"],
            "2":["aaa3","bbb3","ccc3","ddd3"]
          }
        },
        "SellOut":{
          "title":["W01","W02","W03","W04"],
          "sellOut":["aaa1","bbb1","ccc1","ddd1"],
          "inv":["aaa1","bbb1","ccc1","ddd1"]
        },
        "LastStock":{
          "title":["W01","W02","W03","W04"],
          "row1":["aaa1","bbb1","ccc1","ddd1"],
          "row2":["aaa1","bbb1","ccc1","ddd1"]
        }
      }
    }
  },
  methods:{
    initHeight(){
      var h = window.innerHeight;
      // var h = window.screen.height;
      var hh = window.innerHeight - 50;
      // var hh = window.screen.height - 50;
      document.getElementById('content-div-subpage').setAttribute("style","height:"+h+"px");
      document.getElementById('subPage-div').setAttribute("style","height:"+hh+"px");
    },
    pageBack(){
      this.$store.commit('closeSubPage');
    },
    setChart(){
      
      if(_chart01){
        _chart01.destroy();
      }
      if(_chart02){
        _chart02.destroy();
      }
      
      let chartColor = this.$store.state.chartColor;
    
      var ctx01 = document.getElementById('subPageChart01');
      _chart01 = new Chart(ctx01, {
          type: 'doughnut',
          data: {
              labels: this.customerData.chart.chart1.title,
              datasets: [{
                  label: this.customerData.chart.chart1.title,
                  data: this.customerData.chart.chart1.row,
                  backgroundColor: chartColor,
                  borderColor: chartColor,
                  borderWidth: 0,
              }],
          },
          options: {
            aspectRatio: 2,
            legend: {
              labels: {
                  width: 50,
                  fontColor: '#fff',
                  fontFamily: 'Microsoft JhengHei'
              },
              position : "left"
            },
          }
      });

      var ctx02 = document.getElementById('subPageChart02');
      _chart02 = new Chart(ctx02, {
          type: 'line',
          data: {
              labels: this.customerData.chart.chart2.title,
              datasets: [{
                  label: this.customerData.chart.chart2.name,
                  data: this.customerData.chart.chart2.row,
                  pointBorderColor: "#fff",
                  pointBackgroundColor: "#ff6384",
                  backgroundColor: 'rgba(255,255,255,0.1)',
                  borderWidth: 2,
                  borderColor: '#ff6384',
                  lineTension: 0 //使用折線 不要曲線
              }],
          },
          options: {
            aspectRatio: 2,
            legend: {
              labels: {
                  width: 50,
                  fontColor: '#fff',
                  fontFamily: 'Microsoft JhengHei'
              },
              position : "top"
            },
            
            scales: {
                yAxes: [{
                    ticks: {
                        beginAtZero: true,
                        fontColor: '#fff',
                        fontFamily: 'Microsoft JhengHei'
                    },
                }],
                xAxes: [{
                    ticks: {
                        beginAtZero: true,
                        fontColor: '#fff',
                        fontFamily: 'Microsoft JhengHei'
                    },
                }]
            }
          }
      });

    },
    openSlide(id){
      switch(id){
        case 1:
          if(this.slideStatus_1 == ''){
            this.slideStatus_1 = "active";
          }else{
            this.slideStatus_1 = "";
          }
          break;
        case 2:
          if(this.slideStatus_2 == ''){
            this.slideStatus_2 = "active";
          }else{
            this.slideStatus_2 = "";
          }
          break;
        case 3:
          if(this.slideStatus_3 == ''){
            this.slideStatus_3 = "active";
          }else{
            this.slideStatus_3 = "";
          }
          break;
        case 4:
          if(this.slideStatus_4== ''){
            this.slideStatus_4 = "active";
          }else{
            this.slideStatus_4 = "";
          }
          break;
        case 5:
          if(this.slideStatus_5 == ''){
            this.slideStatus_5 = "active";
          }else{
            this.slideStatus_5 = "";
          }
          break;
        case 6:
          if(this.slideStatus_6 == ''){
            this.slideStatus_6 = "active";
          }else{
            this.slideStatus_6 = "";
          }
          break;
        default:
          this.slideStatus_1 = "";
          this.slideStatus_2 = "";
          this.slideStatus_3 = "";
          this.slideStatus_4 = "";
          this.slideStatus_5 = "";
          this.slideStatus_6 = "";
      }
      
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped src="@/assets/css/page.css"></style>
<style scoped>
.content-div-subpage{
  position: fixed;
  z-index: 10;
  top: 0;
  left: 100%;
  width: 100%;
  height: 100vh;
  overflow-y: hidden;
  overflow-x: hidden;
  /* background: #212335; */
  background: #43648B;
    transition: 0.3s all;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
    background: linear-gradient(135deg,#4e505f,#212335);
  /* background: linear-gradient(90deg,#212335,#4e505f,#212335); */
  /* background: linear-gradient(180deg, #43648B 0%, #6FA1D1 48.44%, #2F76B8 100%); */
  /* background: linear-gradient(270deg, #43648B 0%, #6FA1D1 48.44%, #2F76B8 100%); */
}

.content-div-subpage.active{
  left: 0;
}

.subPage-div{
  position: absolute;
  z-index: 1;
  top: 50px;
  width: 100%;
  left: 0;
  overflow-y: auto;
  overflow-x: hidden;
  /* background: #f00; */
}

.home-div-cover{
    position: fixed;
    z-index: 0;
    width: 130%;
    height: 100%;
    left: -15%;
    top: 0px;
    overflow: hidden;
    text-align: center;
}

.home-div-cover div{
    position: relative;
    display: inline-block;
    width: 125px;
    text-align: center;
    line-height: 36px;
    font-size: 30px;
    color: rgba(255,255,255,0.05);
    transform: rotate(30deg);
}
/* #subPageChart01,
#subPageChart02{
  width: 100%;
} */


.window-box-cover{
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0px;
  width: 100%;
  height: calc(100% - 0px);
  background: rgba(0,0,0,0.7);
}
.window-box{
  position: relative;
  margin: 0 auto;
  margin-top: calc(50vh - 25vh);
  /* display: inline-block; */
  width: calc(100% - 30px);
  max-width: 500px;
  background: rgba(62, 65, 79,1);
  border-radius: 5px;
  padding: 15px;
  min-height: 50vh;
}

.header{
    position: absolute;
    width: 100%;
    height: 50px;
    top: 0;
    left: 0;
    z-index: 1;
    /* background: #212335; */
    background: #0f101b;
    padding: 0;
    line-height: 50px;
    color: #fff;
    text-align: center;
  }

  .pageBack-btn{
    position: absolute;
    left: 0;
    top: 0;
    width: 50px;
    height: 50px;
    text-align: center;
    line-height: 50px;
    cursor: pointer;
    transition: 0.2s all;
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
  }

  .pageBack-btn:hover{
    background: #4e505f;
  }

  .pageBack-btn img{
    transform: rotate(-90deg);
  }
.slideBox{
  position: relative;
  width: 100%;
  height: auto;
}
.slideTitle{
  position: relative;
  width: 100%;
  height: auto;
  min-height: 40px;
  line-height: 40px;
  background: rgba(0,0,0,0.5);
  color: #fff;
  padding: 0 15px;
}

.slideTitle img{
  position: relative;
  float: right;
  margin-top: 10px;
}

.slideContent{
  position: relative;
  width: 100%;
  height: auto;
  min-height: 40px;
  line-height: 40px;
  background: rgba(0,0,0,0.3);
  color: #ccc;
  padding: 0 15px;
}

.table_type_1,
.table_type_2{
  width: 100%;
}

.table_type_1 th,
.table_type_1 td{
  border-top: 1px solid #999;
}



.table_type_1 tr:nth-child(1) th,
.table_type_1 tr:nth-child(1) td{
  border-top: 0px solid #999;
}

.table_type_2 th{
  border-bottom: 2px solid #999;
  text-align: center;
}
.table_type_2 td{
  border-top: 1px solid #999;
  text-align: center;
}


.table_type_1 th span,
.table_type_1 td span,
.table_type_2 th span,
.table_type_2 td span{
  color: #fff
}

.my-custom-class .VueCarousel-pagination .VueCarousel-dot-container .VueCarousel-dot{
  width: 30px !important;
  height: 10px !important;
  border-radius: 0 !important;
  
} 
.userLeveldiv{
  position: absolute;
  right: 0;
  width: auto;
  height: 50px;
}

@media (max-width: 413px){
  .table_type_1,
  .table_type_2{
    font-size: 10px;
  }
}

</style>
