<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-12 p-0 " id="powerBiDiv">
        <div class="changePowerBiPageDiv text-center">
          <div class="left-div" @click="backBIdata"><img src="@/assets/images/icon-arrow.svg" height="25" alt=""></div>
            {{pageBiDataType[pageBiDataStatus] ? pageBiDataType[pageBiDataStatus].name : ''}}
          <div class="statusDiv">
            <span v-for="key in pageBiDataType" :key="key.id" :class="key.status?'active':''"></span>
          </div>
          <div class="right-div" @click="nextBIdata"><img src="@/assets/images/icon-arrow.svg" height="25" alt=""></div>
        </div> 
        <div v-for="key in pageBiDataType" :key="key.id"  class="powerBiIframe" v-show="key.status"  :class="key.status?'active':''" :id="'powerBiIframe'+key.id" width="100%">
        </div>
      </div>
    </div>
  </div>
</template>
<script src="https://microsoft.github.io/PowerBI-JavaScript/demo/node_modules/powerbi-client/dist/powerbi.js"></script>

<script>
import * as pbi from 'powerbi-client';

import axios from "axios"; //載入 axios
// import vueAxios from "vue-axios"; //載入 vue-axios

// var models = window['powerbi-client'].models;

      let powerbi = new pbi.service.Service(pbi.factories.hpmFactory, pbi.factories.wpmpFactory, pbi.factories.routerFactory);
      var _pbi = null;
export default {
  props: {
    
  },
  mounted:function(){
    
    this.$store.state.loading_status = true;
    this.getPowerBiData();
    // this.initBi();
    // window.onresize = () => {
    //   this.initBi()
    // }
  },
  data(){
    return {
      
      config : [
        {
          type: 'report',
          tokenType:  pbi.models.TokenType.Embed,
          accessToken: null,
          embedUrl:null,
          id: null,
          permissions: pbi.models.Permissions.All,
          pageView: 'actualSize',
          settings: {
            background: pbi.models.BackgroundType.Transparent,
            filterPaneEnabled: false,
            navContentPaneEnabled: false,
            layoutType: this.readBilayoutType(),
            customLayout: {
                displayOptions: pbi.models.DisplayOption.ActualSize
            },
            localeSettings: {
              language: "en",
              formatLocale: "es"
            }
          }
        },
        {
          type: 'report',
          tokenType:  pbi.models.TokenType.Embed,
          accessToken: null,
          embedUrl:null,
          id: null,
          permissions: pbi.models.Permissions.All,
          pageView: 'actualSize',
          settings: {
            background: pbi.models.BackgroundType.Transparent,
            filterPaneEnabled: false,
            navContentPaneEnabled: false,
            layoutType: this.readBilayoutType(),
            customLayout: {
                displayOptions: pbi.models.DisplayOption.ActualSize
            },
            localeSettings: {
              language: "en",
              formatLocale: "es"
            }
          }
        },
        {
          type: 'report',
          tokenType:  pbi.models.TokenType.Embed,
          accessToken: null,
          embedUrl:null,
          id: null,
          permissions: pbi.models.Permissions.All,
          pageView: 'actualSize',
          settings: {
            background: pbi.models.BackgroundType.Transparent,
            filterPaneEnabled: false,
            navContentPaneEnabled: false,
            layoutType: this.readBilayoutType(),
            customLayout: {
                displayOptions: pbi.models.DisplayOption.ActualSize
            },
            localeSettings: {
              language: "en",
              formatLocale: "es"
            }
          }
        },
        {
          type: 'report',
          tokenType:  pbi.models.TokenType.Embed,
          accessToken: null,
          embedUrl:null,
          id: null,
          permissions: pbi.models.Permissions.All,
          pageView: 'actualSize',
          settings: {
            background: pbi.models.BackgroundType.Transparent,
            filterPaneEnabled: false,
            navContentPaneEnabled: false,
            layoutType: this.readBilayoutType(),
            customLayout: {
                displayOptions: pbi.models.DisplayOption.ActualSize
            },
            localeSettings: {
              language: "en",
              formatLocale: "es"
            }
          }
        }
      ],
      pageBiDataStatus:0,
      pageBiDataType:[
        {id: "0",name:"",status: true,accessToken: null,embedUrl: null,powerBiId: null , open: true},
        {id: "1",name:"",status: false,accessToken: null,embedUrl: null,powerBiId: null , open: true},
        {id: "2",name:"",status: false,accessToken: null,embedUrl: null,powerBiId: null , open: true},
        {id: "3",name:"",status: false,accessToken: null,embedUrl: null,powerBiId: null , open: true}
      ]
    }
  },
  methods:{
    getPowerBiData(){
      
      var _this = this;
      axios.post('https://aorus-southeastasia-api-testing.azurewebsites.net/Api/PowerBi/GetPowerBiInfomation',
      // axios.post('http://tpe-tu170191-nb:9973/Api/PowerBi/GetPowerBiInfomation',
      {
        // "Level": _this.$store.state.GBLoginInfo.user_auth
        "EmployeeID": _this.$store.state.GBLoginInfo.user_id
        // '':''
      }
      )
      .then(response=>{
        this.pageBiDataType = response.data.result.data_list;
        this.initBi();
      })
      .catch(function (error) {
          _this.$store.state.main_status = '3';
          _this.$store.state.loading_status = false;
      });
    },
    readBilayoutType(){
      var ww= window.innerWidth;
      
      if(ww > 990){
        return pbi.models.LayoutType.Custom;
      }else{
        return pbi.models.LayoutType.MobilePortrait;
      }
      
    },
    initBi(){
      var HH = document.getElementById("content-div-home").offsetHeight - 40;     
      document.getElementById("powerBiDiv").setAttribute("style","height:"+HH+"px");


      var biNum = this.pageBiDataType.length;
      if(biNum!=0){
            var key = 0;
            this.config[key].accessToken = this.pageBiDataType[key].accessToken;
            this.config[key].embedUrl = this.pageBiDataType[key].embedUrl;
            this.config[key].id = this.pageBiDataType[key].powerBiId;
            var _iframe = 'powerBiIframe'+this.pageBiDataType[key].id; 
            document.getElementById(_iframe).setAttribute("style","height:"+HH+"px");
            var reportContainer = document.getElementById(_iframe);
            // _pbi = powerbi.embed(reportContainer, this.config[key]);
            var _this = this;
            _pbi = powerbi.bootstrap(reportContainer, this.config[0]);
            // _pbi = powerbi.embed(reportContainer, this.config[0]);
            _pbi.on("loaded",function(){
                _this.$store.state.loading_status = false;

                if(biNum > 1){
                  for(var key=1; key<biNum; key++){
                    _this.config[key].accessToken = _this.pageBiDataType[key].accessToken;
                    _this.config[key].embedUrl = _this.pageBiDataType[key].embedUrl;
                    _this.config[key].id = _this.pageBiDataType[key].powerBiId;
                    if(_this.config[key].accessToken == '' || _this.config[key].accessToken==null) break;
                    var _iframe = 'powerBiIframe'+_this.pageBiDataType[key].id; 
                    document.getElementById(_iframe).setAttribute("style","height:"+HH+"px");
                    var reportContainer = document.getElementById(_iframe);

                    powerbi.bootstrap(reportContainer, _this.config[key]);
                    // powerbi.embed(reportContainer, _this.config[key]);
                    var iframeWindow =  document.getElementById(_iframe).childNodes[0]; 
                    iframeWindow.style.border = 'none';
                    // iframeWindow.style.paddingLeft = '20px';
                  }
                }
                console.log('PowerBI Iframe載入完成 -- ');
            });
            _pbi.on("rendered",function(){
                console.log('PowerBI載入完成 -- ');
                //PowerBI載入完成
            });
            _pbi.on("error", function () {
                // this.report.off("error");
            });
                

            var iframeWindow =  document.getElementById(_iframe).childNodes[0]; 
            iframeWindow.style.border = 'none';
            iframeWindow.style.paddingLeft = '20px';
      }else{
          _this.$store.state.loading_status = false;
      }   
      
    },
    nextBIdata(){
      this.pageBiDataType[this.pageBiDataStatus].status = false;
      this.pageBiDataStatus = this.pageBiDataStatus+1;
      if(this.pageBiDataType.length <= this.pageBiDataStatus){
        this.pageBiDataStatus=0;
      }
      this.pageBiDataType[this.pageBiDataStatus].status = true;

      if(this.pageBiDataType[this.pageBiDataStatus].open != true){
        var HH = document.getElementById("content-div-home").offsetHeight - 40;    
        var _iframe = 'powerBiIframe'+this.pageBiDataType[this.pageBiDataStatus].id; 
        document.getElementById(_iframe).setAttribute("style","height:"+HH+"px");
        var reportContainer = document.getElementById(_iframe);
        powerbi.embed(reportContainer, this.config[this.pageBiDataStatus]);
        this.pageBiDataType[this.pageBiDataStatus].open = true;
      }
      
    },
    backBIdata(){
      this.pageBiDataType[this.pageBiDataStatus].status = false;
      this.pageBiDataStatus = this.pageBiDataStatus-1;
      if(this.pageBiDataStatus < 0){
        this.pageBiDataStatus= this.pageBiDataType.length-1;
      }
        this.pageBiDataType[this.pageBiDataStatus].status = true;

        


      if(this.pageBiDataType[this.pageBiDataStatus].open != true){
        var HH = document.getElementById("content-div-home").offsetHeight - 40;    
        var _iframe = 'powerBiIframe'+this.pageBiDataType[this.pageBiDataStatus].id; 
        document.getElementById(_iframe).setAttribute("style","height:"+HH+"px");
        var reportContainer = document.getElementById(_iframe);
        powerbi.embed(reportContainer, this.config[this.pageBiDataStatus]);
        this.pageBiDataType[this.pageBiDataStatus].open = true;
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped src="@/assets/css/page.css"></style>
<style scoped>
  .page404{
    text-align: center;
    padding: 15px;
    font-size: 6vw;
    width: 700;
  }
  .powerBiIframe{
    position: absolute;
    display: none;
    z-index: 10;
    width: 100%;
    right: 0;
  }
  .powerBiIframe iframe{
    border: none !important;
  }
  .powerBiIframe.active{
    display: inline-block;
  }
  .changePowerBiPageDiv{
    /* position: absolute; */
    position: relative;
    /* z-index: 500; */
    width: 100%;
    height: 40px;
    /* bottom: 0; */
    /* top: 0; */
    /* left: 0; */
    /* background: rgba(0,0,0,0.3); */
    /* background: #333333; */
    background: rgba(255,255,255,0.4);
    /* border-radius: 25px; */
    transition: 0.3s all;
  }
  .changePowerBiPageDiv .statusDiv{
    position: absolute;
    left: calc(50% - (100% - 100px)/2);
    width: calc(100% - 100px);
    height: 25px;
    bottom: 0;
    text-align: center;
  }
  .statusDiv span{
    position: relative;
    display: inline-block;
    width: 10px;
    height: 10px;
    background: rgba(255,255,255,0.3);
    border-radius: 50%;
    margin-right: 10px;
  }
  .statusDiv span:nth-last-child(1){
    margin-right: 0;
  }
  .statusDiv span.active{
    background: rgba(255,255,255,1);
  }

  .changePowerBiPageDiv .left-div,
  .changePowerBiPageDiv .right-div{
    position:absolute;
    z-index: 0;
    top: 0;
    width: 50px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    cursor: pointer;
  }
  
  .changePowerBiPageDiv .left-div{
    left: 0;
  }

  .changePowerBiPageDiv .right-div{
    right: 0;
  }

  .changePowerBiPageDiv .left-div img{
    transform: rotate(-90deg);
  }
  .changePowerBiPageDiv .right-div img{
    transform: rotate(90deg);
  }

  .changePowerBiPageDiv .left-div:hover,
  .changePowerBiPageDiv .right-div:hover{
    /* background: rgba(0,0,0,0.7); */
    background: rgba(255,255,255,0.5);
    /* width: 200px;。 */
  }
  iframe .reportContainerContent{
    background: rgba(0,0,0,0);
  }
  iframe .reportContainerContent .embeddedPV{
    background: rgba(0,0,0,0);
  }
    
</style>
